/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import { FormDropdown, FormInputMask, FormInputText } from 'ui/components/molecues';
import { useFormContext } from 'react-hook-form';
import { VOIVODESHIPS } from 'domain/data';
import { useTranslation } from 'react-i18next';

type AddressRegistrationFieldsProps = {
  name: string,
  disabled?: boolean
}

const AddressRegistrationFields = (props: AddressRegistrationFieldsProps) => {
  const { name, disabled } = props;
  const { control } = useFormContext();
  const { t } = useTranslation();

  const voivodeships = Object.keys(VOIVODESHIPS).map((name) => ({
    id: VOIVODESHIPS[name as keyof typeof VOIVODESHIPS],
    name,
  }));

  return (
    <>
      <div className="field col-12 md:col-6">
        <FormInputText
          id={`${name}.streetName`}
          control={control}
          name={`${name}.streetName`}
          label={t('street')}
          defaultValue=""
          disabled={disabled}
        />
      </div>
      <div className="field col-6 md:col-3">
        <FormInputText
          id={`${name}.streetNumber`}
          control={control}
          name={`${name}.streetNumber`}
          label={t('streetNumber')}
          defaultValue=""
          disabled={disabled}
        />
      </div>
      <div className="field col-6 md:col-3">
        <FormInputText
          id={`${name}.apartmentNumber`}
          control={control}
          name={`${name}.apartmentNumber`}
          label={t('apartmentNumber')}
          defaultValue=""
          disabled={disabled}
        />
      </div>
      <div className="field col-6 md:col-3">
        <FormInputMask
          id={`${name}.zipCode`}
          control={control}
          name={`${name}.zipCode`}
          label={t('zipCode')}
          defaultValue=""
          mask="99-999"
          autoClear={false}
          disabled={disabled}
        />
      </div>
      <div className="field col-6 md:col-3">
        <FormInputText
          id={`${name}.city`}
          control={control}
          name={`${name}.city`}
          label={t('city')}
          defaultValue=""
          disabled={disabled}
        />
      </div>
      <div className="field col-12 md:col-6">
        <FormDropdown
          id={`${name}.voivodeship`}
          control={control}
          name={`${name}.voivodeship`}
          label={t('voivodeship')}
          defaultValue=""
          options={voivodeships}
          optionLabel="id"
          optionValue="name"
          disabled={disabled}
        />
      </div>
    </>
  );
};

export { AddressRegistrationFields };
