/* eslint-disable react/jsx-props-no-spreading */
import React, { useCallback, useState } from 'react';
import { FormProvider, useForm } from 'react-hook-form';
import styled from 'styled-components';
import { Button, Card, Steps } from 'ui/components/atoms';
import { useTranslation } from 'react-i18next';
import { Plenipotentiary, Staff } from 'domain/entities';
import {
  StaffRegistrationForm,
} from '../staff-registration-form/staff-registration-form';
import {
  PlenipotentiaryRegistrationForm,
} from '../plenipotentiary-registration-form/plenipotentiary-registration-form';
import { DemandRegistrationForm } from '../demand-registration-form/demand-registration-form';

const StyledCard = styled(Card)`
  .p-card-body {
    .p-card-content {
      min-height: 44rem;
    }
  }
`;

const CurrentForm = styled.div`
  min-height: 36.125rem;
`;

const StyledButton = styled(Button)`
  width: 100%;
  display: block;
`;

const StyledSteps = styled(Steps)`
  margin-bottom: 2rem;
  .p-steps-item {
    width: 33.333%;
  }
`;

enum registrationStep {
  staff = 0,
  delivery = 1,
  plenipotentiary = 2,
}

export interface RegistrationFormInput {
  staffData: Staff,
  plenipotentiary: Plenipotentiary & {
    gdprConsent: boolean
  }
}

// eslint-disable-next-line no-alert
const onSubmit = <T, >(data: T) => alert(JSON.stringify(data));

interface RegistrationFormProps {
  className?: string;
}

const RegistrationForm = (props: RegistrationFormProps) => {
  const { className } = props;
  const [currentStep, setCurrentStep] = useState<registrationStep>(
    registrationStep.staff,
  );

  const staffMethods = useForm<RegistrationFormInput>();

  const triggerSubmit = useCallback(() => {
    staffMethods.handleSubmit(onSubmit)();
  }, []);

  const goBack = useCallback(() => {
    setCurrentStep(Math.max(registrationStep.staff, currentStep - 1));
  }, [currentStep]);

  const goForward = useCallback(() => {
    setCurrentStep(Math.min(registrationStep.plenipotentiary, currentStep + 1));
  }, [currentStep]);

  const { t } = useTranslation();

  const renderStep = useCallback(
    (currentStep: registrationStep) => {
      switch (currentStep) {
        case registrationStep.staff:
          return (
            <StaffRegistrationForm />
          );
        case registrationStep.delivery:
          return (
            <DemandRegistrationForm />
          );
        case registrationStep.plenipotentiary:
          return (
            <PlenipotentiaryRegistrationForm />
          );
        default:
          return (
            <StaffRegistrationForm />
          );
      }
    },
    [currentStep],
  );

  const renderButtons = useCallback(
    (currentStep: registrationStep) => {
      switch (currentStep) {
        case registrationStep.plenipotentiary:
          return (
            <>
              <div className="col-12 md:col-offset-4 md:col-4">
                <StyledButton onClick={goBack}>{t('goBack')}</StyledButton>
              </div>
              <div className="col-12 md:col-4">
                <StyledButton onClick={triggerSubmit}>
                  {t('register')}
                </StyledButton>
              </div>
            </>
          );
        case registrationStep.staff:
          return (
            <div className="col-12 md:col-offset-8 md:col-4">
              <StyledButton onClick={goForward}>{t('goForward')}</StyledButton>
            </div>
          );
        default:
          return (
            <>
              <div className="col-12 md:col-offset-4 md:col-4">
                <StyledButton onClick={goBack}>{t('goBack')}</StyledButton>
              </div>
              <div className="col-12 md:col-4">
                <StyledButton onClick={goForward}>{t('goForward')}</StyledButton>
              </div>
            </>
          );
      }
    },
    [currentStep],
  );

  return (
    <StyledCard title="Rejestracja" className={className}>
      <StyledSteps
        activeIndex={currentStep}
        model={[
          { label: t('staff') },
          { label: t('materials') },
          { label: t('plenipotentiary') },
        ]}
        onSelect={(e) => setCurrentStep(e.index)}
        readOnly={false}
      />
      <div className="grid">
        <div className="col-12 md:col-12">
          <FormProvider {...staffMethods}>
            <CurrentForm>{renderStep(currentStep)}</CurrentForm>
          </FormProvider>
        </div>
        {renderButtons(currentStep)}
      </div>
    </StyledCard>
  );
};

export { RegistrationForm };
